import React from 'react';
import PropTypes from 'prop-types';
import './App.css';

class TextInput extends React.Component {
	constructor(props) {
		super(props);
		this.state = {value: ''};
		this.inputRef = React.createRef();
	}

	componentDidMount() {
		this.inputRef.current.focus();
	}

	tookInput = (e) => {
		this.setState({value: e.target.value});
	}

	render() {
		return (
			<label className="text-label">
				{this.props.target}
				<input id='input' type="text" ref={this.inputRef} onChange={this.tookInput} className="text-input"/>
			</label>
		);
	}
}

TextInput.propTypes = {
	target: PropTypes.string
};

class App extends React.Component {
	componentDidMount() {
		console.log("Parent Mounted");
	}

	render() {
		return (
			<TextInput target="test"/>
		);
	}
}

export default App;
